.statistics {
    display: inline-flex;
    flex-direction: column;
    box-shadow: 0 2px 2px rgba(2, 2, 2, 0.02);
    background-color: #fff;
    margin-bottom: 30px;
  }
  .sectionTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    color: #475965;
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
  }
  .statList {
    display: flex;
    list-style: none;
    padding: 0;
  }
  .item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border: 1px solid white;
  }
  .labelStyle {
    color: #fff;
    font-family: sans-serif;
    font-size: 12px;
    font-weight: 300;
  }
  .percentageStyle {
    color: #fff;
    font-family: sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
  }