.transactionHistory {
    width: 872px;
    height: 300px;
    box-shadow: 0 3px 4px rgba(0, 0, 0, 0.25);
    background-color: #fff;
  }
  .tableHead{
    background-color: #00bcd4;
    color: #fff;
    font-family: sans-serif;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.17px;
  }
  .tableHead tr{
      height: 50px;
  }
  .transaction {
    color: #5c6975;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 0.45px;
    height: 50px;
    text-align: center;
  }
  .transaction:nth-of-type(even) {
      background-color: #ecf2f3;
  }